export default {
	name: 'jsonView',
	props: {
		data: { // json
			type: [Object, Array],
			required: true
		},
		jsonKey: { // jsonkeykey
			type: String,
			default: ''
		},
		closed: { // 
			type: Boolean,
			default: false
		},
		isLast: { //
			type: Boolean,
			default: true
		},
		fontSize: { //
			type: Number,
			default: 14
		},
		lineHeight: { //
			type: Number,
			default: 24
		},
		deep: { // 
			type: Number,
			default: 3
		},
		currentDeep: { // 
			type: Number,
			default: 1
		},
		iconStyle: { // icon
			type: String,
			default: 'square'
		},
		iconColor: { //icon
			type: Array,
			default () {
				return []
			}
		},
		theme: { // 
			type: String,
			default: ''
		},
		hasSiblings: { // 
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			innerclosed: true,
			templateDeep: 1,
			visible: this.currentDeep < 4
		}
	},
	computed: {
		isArray() {
			return this.getDataType(this.data) === 'array'
		},
		length() {
			return this.isArray ? this.data.length : Object.keys(this.data).length
		},
		subfix() {
			const data = this.data
			if (this.isEmptyArrayOrObject(data)) { // 
				return ''
			} else {
				return (this.isArray ? ']' : '}') + (this.isLast ? '' : ',')
			}
		},
		prefix() {
			return this.isArray ? '[' : '{'
		},
		items() {
			const json = this.data
			if (this.isArray) {
				return json.map(item => {
					const isJSON = this.isObjectOrArray(item)
					return {
						value: item,
						isJSON,
						key: ''
					}
				})
			}
			return Object.keys(json).map(key => {
				const item = json[key]
				const isJSON = this.isObjectOrArray(item)
				return {
					value: item,
					isJSON,
					key
				}
			})
		},
		iconColors() {
			const { theme, iconColor } = this
			if (iconColor.length === 2) {
				return iconColor
			} else {
				if (theme === 'one-dark') {
					return ['#747983', '#747983']
				} else if (theme === 'vs-code') {
					return ['#c6c6c6', '#c6c6c6']
				} else {
					return ['#747983', '#747983']
				}
			}
		}
	},
	mounted() {
		this.innerclosed = this.closed
		this.templateDeep = this.currentDeep
		setTimeout(() => {
			this.visible = true
		}, 0)
	},
	methods: {
		getDataType(data) {
			return Object.prototype.toString.call(data).slice(8, -1).toLowerCase()
		},
		isObjectOrArray(source) {
			return ['array', 'object'].includes(this.getDataType(source))
		},
		toggleClose() {
			if (this.length === 0) {
				return
			}
			if (this.innerclosed) {
				this.innerclosed = false
			} else {
				this.innerclosed = true
			}
		},
		isClose(curDeep) {
			return curDeep > this.deep
		},
		isEmptyArrayOrObject(data) { // 
			return [{},
				[]
			].map(item => JSON.stringify(item)).includes(JSON.stringify(data))
		},
	},
	watch: {
		closed() {
			this.innerclosed = this.closed
		}
	}
}



